
.list-container {
    height: 100%;
    width: 30%;
    min-width: 6.5rem;
    max-width: 15rem;
    padding-top: 1.5%;
    background: black;
    position: relative;
}

.list {
    
   width: 100%;
   height: 97.5%;
   position: relative;
   left: -5px;
    overflow-y: auto;
    z-index: 4;
    scrollbar-color:red black;
    scrollbar-width: thin;
    scroll-behavior: auto;
    padding: 1.5%;
    direction:rtl;
}

.list-item {
    width: 100%;
    max-width: 15rem;
    height: 18vh;
    min-height: 4rem;
    max-height: 10rem;
    margin-bottom: 2%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: whitesmoke;
    font-weight: bold;
    color: black;
    font-size: 2vmax;
    user-select: none;
    border: none;
    direction:ltr;
}

.selected-list-item {
    width: 100%;
    max-width: 15rem;
    height: 18vh;
    min-height: 4rem;
    max-height: 10rem;
    margin-bottom: 2%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: greenyellow;
    font-weight: bold;
    color: black;
    font-size: 2vmax;
    user-select: none;
    border: none;
    left: 1px;
    direction:ltr;
    transition: 10ms;
}

.list::-webkit-scrollbar {
    width: 0.5rem;
    
  }
   
  .list::-webkit-scrollbar-thumb {
    background-color: rgba(255, 0, 0, 0.616);
  }